.opened-chat {
    display: grid;
    grid-template-rows: 56px 1fr;
    overflow: hidden;

    .chat-info {
        background: white;
        border-bottom:  1px solid #DADCE0;
    }

    .messages-section {
        overflow: hidden;
        display: grid;
        justify-items: center;
       
        .messages-box {
            overflow: hidden;
            height: 100%;
            max-width: 840px;
            padding: 0 16px 0 16px;
            display: grid;
            grid-template-rows: 1fr 140px ;
            
            .messages-container {
                overflow-y: auto;
                display: flex;
                flex-direction: column-reverse;
                gap: 8px;
        
                .message-bubble {
                    border-radius: 1em;
                    padding: 8px;
                    max-width: 70%;
                    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
                }
        
                // message bubble type
                .your-message {
                    background: gray;
                    align-self: flex-end;
                }
                .senders-message {
                    background: white;
                    align-self: flex-start;
                }
            }

            // message input
            .message-input {
                padding-top: 16px;
                border-top: 1px solid white;
                display: flex;
                gap: 8px;

                .inputbox {
                    background: white;
                    width: 100%;
                    height: 42px;
                    border-radius: 16px;  
                    padding: 8px;          
                    display: grid;
                    grid-template-columns: 40px 1fr 40px;
                    gap: 8px;
                    align-items: center;
                    justify-items: center;

                    .icon {
                        font-size: 25px;
                        color: gray;
                    }

                    input {
                        height: 100%;
                        width: 100%;
                        border: none;
                        background: none;
                        font-size: 16px;
                        color: gray;
                    }
                    
                    input:focus {
                        outline: none;
                    }

                }

                .icon-send-record {
                    background: white;
                    color: gray;
                    height: 30px;
                    width: 36px;
                    padding: 16px;
                    border-radius: 50%;
                }
            }
        }
    }
}